/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul, li {
  list-style: none;
}

blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

button, input, select, textarea {
  background-color: transparent;
  outline: none;
  border: 0;
}

a,
a:hover,
a:active,
a:focus {
  text-decoration: none;
  outline: none;
}

/*
0 - 600px:      Phone
600 - 900px:    Tablet portrait
900 - 1200px:   Tablet landscape
[1200 - 1800] is where our normal styles apply
1800px + :      Big desktop

$breakpoint arguement choices:
- phone
- tab-port
- tab-land
- big-desktop

ORDER: Base + typography > general layout + grid > page layout > components

1em = 16px
*/
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}
body {
  box-sizing: border-box;
  position: relative;
}

a {
  color: #fff;
}

.wrapper {
  max-width: 114rem;
  margin: 0 auto;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 1.5rem;
}

.heading-primary {
  color: #fff;
  font-weight: 700;
  font-size: 6rem;
  margin: 4rem 0;
}

.heading-secondary {
  color: #fff;
  font-weight: 700;
  font-size: 3.6rem;
}

.heading-tertiary {
  color: #fff;
  font-weight: 700;
  font-size: 2.2rem;
}

.paragraph-bold {
  font-weight: 700;
}
.paragraph-italic {
  font-style: italic;
}
.paragraph-light {
  font-weight: 100;
}

.u-light {
  color: #333;
}

.u-margin-bottom-small {
  margin-bottom: 1.5rem !important;
}

.u-margin-bottom-medium {
  margin-bottom: 4rem !important;
}
@media only screen and (max-width: 56.25em) {
  .u-margin-bottom-medium {
    margin-bottom: 3rem !important;
  }
}

.u-margin-bottom-big {
  margin-bottom: 8rem !important;
}
@media only screen and (max-width: 56.25em) {
  .u-margin-bottom-big {
    margin-bottom: 5rem !important;
  }
}

.u-margin-top-big {
  margin-top: 8rem !important;
}

.u-margin-top-huge {
  margin-top: 10rem !important;
}

.button {
  width: 15.5rem;
  height: 3.7rem;
  color: #fff;
  border: 1px solid #fff;
  margin: 0 1.5rem;
  font-weight: 700;
}
.button.primary {
  background: #e84118;
  border: 1px solid #e84118;
}
.button.black {
  border: 1px solid #333;
  color: #333;
  margin-left: 0;
}

.sns {
  margin-bottom: 4rem;
}
.sns i:not(:last-child) {
  margin-right: 2.2rem;
}

.about-me {
  max-width: 114rem;
  margin: 0 auto;
  color: #333;
  display: flex;
  padding: 12rem 0;
}
.about-me__img {
  width: auto;
  max-height: 100%;
  object-fit: cover;
}
.about-me__container {
  padding-left: 3.4rem;
}
.about-me h3 {
  margin: 2.5rem 0;
}
.about-me .sns {
  margin-top: 4rem;
}
.about-me p {
  line-height: 2;
  color: #777;
}
.about-skill {
  background: linear-gradient(rgba(0, 98, 102, 0.9), rgba(0, 98, 102, 0.9)), url(../img/about-bg.jpg);
  background-position: center;
  background-size: cover;
  padding: 12.3rem 0;
}
.about-skill__container {
  max-width: 114rem;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.about-skill__wrapper:not(:last-child) {
  margin-right: 6rem;
}
.about-skill h2 {
  margin-bottom: 4.3rem;
}
.about-skill h3:not(:first-child) {
  margin-top: 3rem;
}
.about-skill p {
  color: #ddd;
}

.header {
  max-width: 114rem;
  margin: 0 auto;
  height: 3rem;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  font-weight: bold;
  color: #fefefe;
  padding-top: 3rem;
  margin: 0 3rem;
}
.header__nav-list {
  display: flex;
  margin: 0 3rem;
}
.header__nav-item {
  padding-top: 3rem;
}
.header__nav-link {
  padding: 0.5rem 1.5rem;
}

.hero {
  height: 84vh;
  background: url(../img/header-bg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.hero__container {
  background: rgba(0, 0, 0, 0.7);
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: #fff;
}